
body {
    /* Size of largest container or bigger */
    min-width: 1200px;
    width: auto !important;
	width: 1200px;
}



.mainDIV{
    position:relative;
    background:yellow;
    width:100%;
    min-width:315px;
}
.leftDIV{
    position:absolute;
    top:0px;
    left:0px;
    height:50px;
    width: 30%;
    background: #555;
    border-right: 1px solid #ccc;
}
.middleDIV{
	position:relative;
	top: 0%;
	left: -2.5%;
    height:50px;
    width: 35%;
    background:#555;
    margin:0px auto;
}
.rightDIV{
    position:absolute;
    top:0px;
    right:0px;
    height:50px;
    width: 35%;
    background:#555;
    border-left: 1px solid #ccc;
}




.mainDIVBody{
    position:relative;
    background: none;
    width:100%;
    min-width:315px;
}
.leftDIVBody{
    position:absolute;
    top:0px;
    left:0px;
    height:600px;
    width: 30%;
    background: none;
    border-right: 1px solid #ccc;
}
.middleDIVBody{
	position:relative;
	top: 0%;
	left: -2.5%;
    height:600px;
    width: 35%;
    background:none;
    margin:0px auto;
}
.rightDIVBody{
    position:absolute;
    top:0px;
    right:0px;
    height:600px;
    width: 35%;
    background:none;
    border-left: 1px solid #ccc;
}






.productList {

	padding: 1em 1em 1em;
    background-color: #ffffff;
    transition: background-color 0.5s ease;
    border: 0px solid #ccc;
    height: auto;
    border-bottom-width: 0px;
    border-left-width: 0px;
    border-right-width: 0px;
}

.productList:nth-last-child(-n+1) {
    border-bottom-width: 0px;
}

#productListName {
	color: #777777;
}

.actives:hover {
	background-color: #5eb8ad;

	#productListName {

		color: #ffffff;
	}
}

.selectProductWithColor {
	border: 3px solid #5eb8ad;
	
}

.tooltip {
	font-size: 1.2em;
}
.tooltip-inner {
	background-color: #5eb8ad;
}
.tooltip-arrow {
	color: #5eb8ad;
}



.navbar-nav > li > a, .navbar-brand {
    padding-top:5px !important; 
    padding-bottom:0 !important;
    height: 30px;
}
.navbar {
	min-height:30px !important;
}



.navbar-default {	
	background-color: #5eb8ad;
	border-color: #5eb8ad;
	border-radius: 0px;
}

.navbar-default .navbar-brand {
	color: #ffffff;
}

.navbar-default .navbar-nav>li>a {
	
	color: #ffffff;
}




.linkList {
	
	border-right-width: 1px; 
	border-left-width: 0px;
	border-top-width: 1px;
	border-bottom-width: 0px;
	border-style: solid; 
	border-color: #ccc; 
	padding: 15px; 
	margin: 0px 0px 0px 0px;
	
}

.linkList:nth-last-child(-n+1) {
    border-bottom-width: 1.33px;
}

.linkList:first-child {
	border-top-width: 0px;
}



